.container {
  margin: 40px 0px 10px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 320px;

  button {
    padding: 8px 10px;
    margin: 0 4px;
    background-color: #1c1b1b;
    color: bisque;
    border: none;
    border-radius: 4px;
    cursor: pointer;

    &:disabled {
      background-color: bisque;
      color: #1c1b1b;
      opacity: 0.5;
      cursor: not-allowed;
    }
  }
  span {
    color: bisque;
    font-size: 13px;
    padding: 8px 10px;
    background-color: #1c1b1b;
    border: none;
    border-radius: 4px;
    margin: 0 4px; /*"..."*/
  }
}
//==========================================
@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}
//==========================================
@media screen and (min-width: 1440px) {
  .container {
    width: 765px;
    button {
      padding: 8px 16px;
    }
    span {
      padding: 8px 16px;
    }
  }
}
